<template>
  <page-layout :title="title">
    <template slot="action">
      <div class="interval-sovle">
        <a-button-group style="margin-right: 8px">
          <a-button
            icon="export"
            @click="showExportModal"
          >
            导出
          </a-button>
        </a-button-group>
        <a-button
          type="primary"
          icon="search"
          @click="showQueryModel"
        >
          <span>查询</span>
          <queryModal ref="dialogtreeQueryModal"></queryModal>
        </a-button>
      </div>
    </template>
    <div class="dailyMonitoringList">
      <!-- <span style="margin-left: 8px">
      <template v-if="hasSelected">
        {{`Selected ${selectedRowKeys.length} items`}}
      </template>
    </span> -->
      <a-table
        :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onSelectChange}"
        :dataSource="dataSource"
        :columns="columns"
        @change="handleChange"
        :scroll="{ x: 1300 }"
        :pagination="pagination"
        :loading="loading"
      >
        <template
          slot="operation"
          slot-scope="text, record"
        >
          <a
            href="javascript:;"
            @click="onDelete(record.key)"
          >踢除</a>
        </template>
      </a-table>
    </div>
  </page-layout>
</template>

<script>
import ATable from "ant-design-vue/lib/table";
import AButton from "ant-design-vue/lib/button";
import queryModal from "./QueryModal";
import PageLayout from "@/components/Page/PageLayout";
const AButtonGroup = AButton.Group
export default {
  props: {
    customerId: String
  },
  components: {
    ATable,
    AButton,
    AButtonGroup,
    queryModal,
    PageLayout
  },
  data() {
    return {
      dataSource: [],
      selectedRowKeys: [],
      loading: false,
      loadingCallHistory: false,
      filteredInfo: null,
      sortedInfo: null,
      pagination: {
        showQuickJumper: true,
        pageSize: 10
      }
    };
  },
  mounted() {
    this.getDataFromServer();
  },
  watch: {
    customerId: {
      handler: function(customerId) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.deleteLocalHistory();
        }, 1000);
      }
    }
  },
  computed: {
    title() {
      return this.$route.meta.title;
    },
    hasSelected() {
      return this.selectedRowKeys.length > 0;
    },
    columns() {
      let { sortedInfo, filteredInfo } = this;
      sortedInfo = sortedInfo || {};
      filteredInfo = filteredInfo || {};
      const columns = [
        {
          title: "姓名",
          dataIndex: "seatName",
          key: "seatName",
          sorter: (a, b) => a.seatName.length - b.seatName.length,
          sortOrder: sortedInfo.columnKey === "seatName" && sortedInfo.order
        },
        {
          title: "工作组",
          dataIndex: "workTeam",
          key: "workTeam",
          sorter: (a, b) => a.workTeam.length - b.workTeam.length,
          sortOrder: sortedInfo.columnKey === "workTeam" && sortedInfo.order
        },
        {
          title: "首拨完成数",
          dataIndex: "firstDial",
          key: "user",
          sorter: (a, b) => a.user.length - b.user.length,
          sortOrder: sortedInfo.columnKey === "user" && sortedInfo.order
        },
        {
          title: "预约完成数",
          dataIndex: "appointment",
          key: "appointment",
          sorter: (a, b) => a.appointment.length - b.appointment.length,
          sortOrder: sortedInfo.columnKey === "appointment" && sortedInfo.order
        },
        {
          title: "成功数",
          dataIndex: "success",
          key: "success",
          sorter: (a, b) => a.success.length - b.success.length,
          sortOrder: sortedInfo.columnKey === "success" && sortedInfo.order
        },
        {
          title: "失败数",
          dataIndex: "failure",
          key: "failure",
          sorter: (a, b) => a.failure.length - b.failure.length,
          sortOrder: sortedInfo.columnKey === "failure" && sortedInfo.order
        },
        {
          title: "日期",
          dataIndex: "createDate",
          key: "createDate",
          sorter: (a, b) => a.createDate.length - b.createDate.length,
          sortOrder: sortedInfo.columnKey === "createDate" && sortedInfo.order
        }
      ];
      return columns;
    }
  },
  methods: {
    onSelectChange(selectedRowKeys) {
      this.selectedRowKeys = selectedRowKeys;
    },
    showExportModal() {},
    getDataFromServer() {
      //TODO:通过AJAX请求获得呼叫历史信息（如果仅显示该坐席的与该客户的呼叫记录，那么参数参数为坐席id+客户id）
      for (let i = 0; i < 25; i++) {
        if ((i + 1) % 2 == 0) {
          this.dataSource.push({
            key: i,
            id: i,
            seatName: `600${i}`,
            workTeam: `工作组-${i}`,
            firstDial: i,
            appointment: i,
            success: i,
            failure: i,
            createDate: `2018-08-${i} ${i}:00:00`
          });
        } else {
          this.dataSource.push({
            key: i,
            id: i,
            seatName: `600${i}`,
            workTeam: `工作组-${i}`,
            firstDial: i,
            appointment: i,
            success: i,
            failure: i,
            createDate: `2018-08-${i} ${i}:00:00`
          });
        }
      }
    },
    deleteLocalHistory() {
      this.dataSource = [];
    },
    handleChange(pagination, filters, sorter) {
      this.filteredInfo = filters;
      this.sortedInfo = sorter;
    },
    onDelete(key) {
      let _this = this;
      this.$confirm({
        title: "你确定要踢除该坐席吗?",
        content: "Some descriptions",
        okText: "确定",
        okType: "danger",
        cancelText: "取消",
        onOk() {
          _this.loading = true;
          // ajax request after empty completing
          setTimeout(() => {
            _this.loading = false;
            const dataSource = [..._this.dataSource];
            _this.dataSource = dataSource.filter(item => item.key !== key);
          }, 1000);
        },
        onCancel() {
        }
      });
    },
    showQueryModel() {
      this.$refs.dialogtreeQueryModal.showQueryModal();
    }
  }
};
</script>

<style>
</style>



